body {
  overflow: hidden;
  margin: 0;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}
canvas {
  position: absolute;
  top: 0;
  left: 0;
  cursor: pointer;
}
h2 {
  position: absolute;
  bottom: 10px;
  width: 100%;
  text-align: center;
  color: white;
  font-family: 'Josefin Sans', sans-serif;
  letter-spacing: 4px;
  opacity: 0.2;
  font-weight: 300;
}